body {
  margin: 0;
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app {
  display: flex;
}
.main {
  background: #17161a;
  flex-grow: 1;
  height: 100vh;
}

.navbar1 {
  width: 65px;
  height: 65px;
  border-radius: 50%;
  background: white;
  margin: auto;
  position: relative;
  text-align: center;
  color: #f50057;
  transition: 0.24s 0.2s;
  top: 45%;
}

.menuTitle {
  position: absolute;
  width: 100%;
  height: 100%;
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
}

.navbar1 .menu1 {
  list-style: none;
  position: absolute;
  top: 15px;
  left: 35px;
  border-radius: 50%;
  transform: scale(0);
  transform: transform 1.4s 0.07s;
  z-index: -1;
}
.navbar1:hover .menu1 {
  transform: scale(1.26);
  transform: transform 0.4s 0.08s, z-index 0s 0.5s;
  z-index: 1;
}
@media (min-width: 375px) {
  .navbar1:hover .menu1 {
    transform: scale(1.5);
  }
}
@media (min-width: 425px) {
  .navbar1:hover .menu1 {
    transform: scale(1.7);
  }
}

.navbar1:hover .menu1 li {
  transition: all 0.7s;
}
.navbar1 i {
  margin-top: 15px;
}

.navbar1 .menu1 li {
  position: absolute;
  top: -80px;
  left: -79px;
  transform-origin: 80px 80px;
  transition: all 0.5s 0.1s;
}

@media (min-width: 768px) {
  .navbar1:hover .menu1 {
    transform: scale(1.9);
  }

  .navbar1 {
    width: 100px;
    height: 100px;
  }
  .navbar1 .menu1 {
    left: 45px;
    top: 15px;
  }
  .navbar1 .menu1 li {
    transform-origin: 90px 90px;
  }
  .menuTitle {
    font-size: 20px;
  }
}

@media (min-width: 1024px) {
  .navbar1:hover .menu1 {
    transform: scale(2.5);
  }
  .navbar1 .menu1 {
    left: -70px;
    top: -50px;
    border: 100px solid transparent;
  }
  .navbar1 .menu1 li {
    top: -95px;
    left: -70px;
  }
}

.navbar1 .menu1 li a {
  width: 60px;
  height: 60px;
  line-height: 5px;
  border-radius: 50%;
  background-color: white;
  position: absolute;
  color: #f50057;
  transition: 0.7s;
  text-decoration: none;
  font-size: 10px;
}
.navbar1:hover .menu1 li:nth-child(1) {
  transition-delay: 0.02s;
  transform: rotate(0deg);
}

.navbar1:hover .menu1 li:nth-child(2) {
  transition-delay: 0.04s;
  transform: rotate(72deg);
}
.navbar1:hover .menu1 li:nth-child(2) a {
  transition-delay: 0.08s;
  transform: rotate(288deg);
}
.navbar1:hover .menu1 li:nth-child(3) {
  transition-delay: 0.06s;
  transform: rotate(144deg);
}
.navbar1:hover .menu1 li:nth-child(3) a {
  transition-delay: 0.12s;
  transform: rotate(216deg);
}
.navbar1:hover .menu1 li:nth-child(4) {
  transition-delay: 0.08s;
  transform: rotate(216deg);
}
.navbar1:hover .menu1 li:nth-child(4) a {
  transition-delay: 0.16s;
  transform: rotate(144deg);
}
.navbar1:hover .menu1 li:nth-child(5) {
  transition-delay: 0.1s;
  transform: rotate(288deg);
}
.navbar1:hover .menu1 li:nth-child(5) a {
  transition-delay: 0.2s;
  transform: rotate(72deg);
}
